<template>
  <el-row>
    <el-col :span="2">
      <el-avatar :size="60" :src="circleUrl" />
    </el-col>
    <el-col :span="8">
      <h1 class="title">IT测评系统后台管理</h1>
    </el-col>
    <el-col :span="8" style="text-align: left">
      <span>欢迎您：{{userName}}</span>
      <br />
      <span>上次登录时间为：{{loginTime}}</span>
    </el-col>
    <el-col :span="6" align="right">
      <el-button class="button" type="primary" icon="el-icon-s-operation" @click="front" circle />
      <el-button class="button" type="warning" icon="el-icon-switch-button" @click="logout" circle />
    </el-col>
  </el-row>
</template>

<script>
import head from "../../assets/head.jpg";

export default {
  data() {
    return {
      circleUrl: head,
      userName: sessionStorage.getItem("userName"),
      loginTime: sessionStorage.getItem("loginTime")
    };
  },
  methods: {
    front() {
      this.$router.push({
        name: "cPaperList",
        params: { current: 1, size: 5 }
      });
    },
    logout() {
      sessionStorage.clear();
      this.$message({
        showClose: true,
        message: "您已退出登录",
        type: "warning"
      });
      this.$router.push({ name: "login" });
    }
  },
  created() {
  }
};
</script>

<style lang="scss" scoped>
.title {
  color: transparent;
  -webkit-text-stroke: 1px black;
  letter-spacing: 0.04em;
  font-size: 25px;
}
.button {
  margin-top: 10px;
}
</style>